@mixin 함수() {
  background-color: rgb(255, 245, 212);
  padding: 20px 0 20px 0;
  width: 100%;
  max-width: 500px;
  margin: auto;
  border-radius: 10px;
}

.my-alert {
  @include 함수();
}

.my-alert p {
  margin-bottom: 0;
}

.my-alert2 {
  @extend .my-alert;
  background-color: #fef5c9;

}

.wow-enter {
  opacity: 0;
}

.wow-enter-active {
  opacity: 1;
  transition: all 500ms;
}

.col-md-2 {
  background-color: #ffeee1;
  border-radius: 10px;

  h6 {
    font-weight: bolder;
  }
}